.header-outer-container{
    position: sticky;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(20,20,20);
    /* background-color: transparent; */
    height: 10vh;
    color: white;
    transition: all 0.5s;
    transition-timing-function: ease-in;
    z-index: 1;

}

.header-container{
    width: 90%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
ul{
    position: relative;
    top: 10px;
    list-style-type: none;
    display: flex;
 margin: 10px;
}
li{
    padding: 20px;
    font-family: 'Times New Roman', Times, serif;
    font-size: 18px;
}
img{
   position: relative;
   left: -40px;
   
}